import { ArabicToPersian } from "utils/ConvertToPersianTextFromArabic";
import { TitleMessages } from "../functions/TitleMessages";

export const CardData = (test) => {
  const [refferralDateTime, referalLocationMsg, titleMsg] = TitleMessages();
  const data = [
    {
      title: refferralDateTime,
      value: `${test?.Date} - ${test?.Time}`,
    },
    {
      title: referalLocationMsg,
      value: ArabicToPersian(test?.MahaleMorajee),
    },
    {
      title: titleMsg,
      value: ArabicToPersian(test?.NameNoeMorajee),
    },
  ];
  return data;
};
