import { ShowResultReport } from "../Adapters/ShowResultReport";

export const HandleShowReport = (
  dev,
  id,
  setForceRender,
  setPreviewImage,
  setLoadingPreview
) => {
  setLoadingPreview(true);

  ShowResultReport(
    { dev, id },
    setForceRender,
    setPreviewImage,
    setLoadingPreview
  );
};
