import React, { useState } from 'react';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import style from '../../styles/card.frame.module.scss';
import {
  Button,
  Card,
  Stack,
  LinearProgress,
} from '@mui/material';
import { CardData } from '../../variable/CardData';
import { Download, Visibility } from '@mui/icons-material';
import { HandleDownloadReport } from '../../functions/HandleDownloadReport';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import { HandleShowReport } from '../../functions/HandleShowReport';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';
import { ToBlob } from 'utils/ToBlob';

const TestResultCards = ({ test, setForceRender }) => {
  const screenWidth = useCheckScreenWidth(); // 1024 for Desktop

  const [previewData, showPreviewData] = useState();
  const [get, set] = useState();

  const datas = CardData(test);
  const downloadMsg = FindLangKey(
    'general-public-btn-download'
  );
  const downlaodingMsg = FindLangKey(
    'general-public-alert-downloading'
  );
  const preview_msg = FindLangKey('general-public-preview');

  return (
    <React.Fragment>
      <Card
        sx={{ mt: '1em', p: '.3em' }}
        className={style?.Card}
      >
        <Stack
          gap="1em"
          bgcolor="white"
          padding=".5em"
          borderRadius="20px"
        >
          {datas?.map((data, index) => (
            <Stack
              direction="row-reverse"
              justifyContent={'space-between'}
              key={index}
            >
              <p>{data?.title}</p>
              <p>{data?.value}</p>
            </Stack>
          ))}
          <Stack
            gap=".5em"
            direction="row-reverse"
            alignItems="center"
            justifyContent="space-between"
          >
            <Button
              variant="contained"
              fullWidth
              sx={{
                color: `${themeColors.ColorPrimary} !important`,
                maxHeight: '40px !important',
              }}
              onClick={() =>
                HandleDownloadReport(
                  test?.NoeIdShakhesDev,
                  test?.ID,
                  set,
                  setForceRender
                )
              }
            >
              {downloadMsg}
            </Button>

            <Button
              fullWidth
              variant="contained"
              sx={{
                color: `${themeColors.ColorPrimary} !important`,
                maxHeight: '40px !important',
              }}
              onClick={() => {
                if (previewData) {
                  showPreviewData(undefined);
                } else {
                  HandleShowReport(
                    test?.NoeIdShakhesDev,
                    test?.ID,
                    setForceRender,
                    showPreviewData,
                    set
                  );
                }
              }}
            >
              {preview_msg}
            </Button>
          </Stack>

          <>
            {get ? (
              <LinearProgress />
            ) : previewData ? (
              // <iframe
              //   src={ToBlob(previewData)}
              //   title={"report"}
              //   style={{
              //     border: "none",
              //     borderRadius: "5px",
              //     width: "100%",
              //   }}
              //   loading="در حال بارگیری"

              // />
              // <>
              //   <Document
              //     file={}
              //     onLoadSuccess={onDocumentLoadSuccess}
              //   >
              //     <Page pageNumber={pageNumber} width={100} height={"20%"} />
              //   </Document>
              //   <p>
              //     Page {pageNumber} of {numPages}
              //   </p>
              // </>

              <></>
            ) : (
              <></>
            )}
          </>
        </Stack>
      </Card>
    </React.Fragment>
  );
};

export default TestResultCards;
