import GetPostResponse from 'Global/Functions/GetPostResponse/GetPostResponse';
import Cookies from 'js-cookie';
import { env } from 'env';

export const ResultList = () => {
  const patient = JSON.parse(Cookies.get('user'));
  const testResults = GetPostResponse(
    `${process.env.REACT_APP_API_URL_API_FIND_TURN}`,
    true,
    {
      idPatient: patient?.IdPatient,
    }
  )?.Data;
  return testResults;
};
