import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";

export const TitleMessages = () => {
  const refferralDateTime = FindLangKey("lab-test-title-date-time");
  const referalLocationMsg = FindLangKey("lab-test-title-referral-location");
  const titleMsg = FindLangKey("lab-test-title-title");
  return [refferralDateTime, referalLocationMsg, titleMsg];
};
