import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { Post } from 'Xhr/Xhr';
import Swal from 'sweetalert2';
import { Base64Downloader } from 'utils/Base64Downloader';
import { v4 as uuidV4 } from 'uuid';
import { env } from 'env';
import { ShowBase64 } from 'utils/ShowBase64';
import { ToBlob } from 'utils/ToBlob';
import { Base64toNewTab } from 'utils/Base64toNewTab';

export const ShowResultReport = async (
  data,
  setForceRender,
  setPreviewImage,
  setLoadingPreview
) => {
  setLoadingPreview(true);
  Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_GET_REPORT}`,
    true,
    {
      noeIdShakhesDev: data.dev,
      itemId: data.id,
    }
  ).then((response) => {
    console.log('response->', response);
    if (response.data.resCode === 1) {
      Base64toNewTab(response.data.Data);
    } else {
      SwalAlert('error', response.data.resMessage, false);
    }
    setLoadingPreview(false);
  });
};
