import { Container, Stack } from '@mui/material';
import React, { useState } from 'react';
import { ResultList } from './functions/ResultList';
import TestResultCards from './components/TestResultCards/TestResultCards';
import useConditionalGrid from 'Global/Hooks/useConditionalGrid';

const LabTestResult = () => {
  const testResults = ResultList();
  const [forceRender, setForceRender] = useState(false);

  const isGrid = useConditionalGrid(
    testResults?.PatientTurnList
  );

  return (
    <React.Fragment>
      {forceRender ? <></> : <></>}
      <Container maxWidth="md">
        <Stack
          display={isGrid ? 'grid' : ''}
          sx={{
            gridTemplateColumns: isGrid
              ? 'repeat(2, 1fr) !important'
              : '',
            direction: 'rtl',
            gap: '.5em',
          }}
        >
          {testResults?.PatientTurnList?.map((test) => (
            <TestResultCards
              test={test}
              key={test?.ID}
              setForceRender={setForceRender}
            />
          ))}
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default LabTestResult;
